import Vue from 'vue';
import VueRouter from 'vue-router';
import app from './App';
import vheader from 'component/mui/Header';

Vue.use(VueRouter);
var router = new VueRouter();

var HeaderVue = Vue.extend(vheader);

var header = new HeaderVue({
    propsData: {
        title: '我的供应信息',
        hdstyle: {
            back: {
                display: true
            },
            title: {
                display: true
            },
            rightButton: {
                display: true,
                text: '编辑',
                onClick: function() {
                    router.app.changeListStatus();
                }
            }
        }
    },
    el: 'vheader'
});
var FooterVue = Vue.extend({
    props: {
        isedit: {
            type: Boolean,
            twoWay: true
        },
        btnclass: {
            type: String,
            twoWay: true
        },
        selecttotal: {
            type: Number,
            twoWay: true
        }
    }
});

var footer = new FooterVue({
    propsData: {
        isedit: false,
        btnclass: 'mui-btn mui-btn-delete mui-btn-half',
        selecttotal: 0
    },
    methods: {
        goToSupplierProductList() {
            router.app.goToSupplierProductList();
        },
        deleteAllSupplierInfo() {
            router.app.deleteAllSupplierInfo();
        }
    },
    el: '#footer'
});

// mui生命周期管理
mui.init({
    swipeBack: true,
    subpages: [{
        url: 'infolist.html',
        id: 'seller_supplier_infolist',
        styles: {
            top: '90px',
            bottom: '49px'
        }
    }]
});

mui.plusReady(function() {
	if(!app.events){
		app.events={};
	}
	app.events["readyFun"]=function() {
		this.header = header, this.footer = footer;
	}
    router.start(app, '#app');
});
